import { Component } from '@angular/core';
import { APIService } from '../../globals/services/API.service';
import { Hunkhtml} from '../../directives/hunkhtml.directive';
import { CkeditorComponent, } from '../../components/ckeditor/ckeditor.component';
import {HtmlcontentsService} from  '../../globals/services/htmlcontents.service';
import { HunkToolbarComponentComponent } from '../../components/hunk-toolbar-component/hunk-toolbar-component.component';
import {ErrorService,MessageTypes,ErrorMsg} from "../../globals/services/error.service";
import {ActivatedRoute} from '@angular/router';
@Component({
  moduleId: module.id,
  selector: 'app-changeforgottenpassord',
  templateUrl: 'changeforgottenpassord.component.html',
  styleUrls: ['changeforgottenpassord.component.css'],
    directives: [Hunkhtml,CkeditorComponent,HunkToolbarComponentComponent],
    providers: [APIService]
})
export class ChangeforgottenpassordComponent  {
    toolbarshow:boolean=false;
    editorshow:boolean=false;
    currentUser:any={
        password:'',
        password2:'',
        reminder:''
    }


  constructor(private _APIService:APIService,public _HtmlcontentsService:HtmlcontentsService,  private _ErrorService:ErrorService,private _ActivatedRoute:ActivatedRoute) {
      this.currentUser.reminder=this._ActivatedRoute.snapshot.params['id'];
  }

    onToolbarLeave(event){
        this.toolbarshow=false;
    }
    onChangeView(viewMode){
        this.editorshow=(viewMode=='edit')?true:false;
    }
    onValueChanged(content){
        this._HtmlcontentsService.contents.changeforgottenpassord.content=content;
    }
    saveContent(){

        this._HtmlcontentsService.setContent( this._HtmlcontentsService.contents.changeforgottenpassord.id,this._HtmlcontentsService.contents.changeforgottenpassord.content);
        this.editorshow=false;
    }
    onSendNewPassword(){
        this._APIService.getResponsePUT('api/user/forgottenpasswordrchange', this.currentUser).subscribe(
                        data=> {

                            this._ErrorService.errorClear();
                            if (data.message) {
                                let error:ErrorMsg = {
                                    type: MessageTypes.mtDanger,
                                    msg: data.message.errors
                                }
                                this._ErrorService.setErrorMsg(error);
                            }
                            else alert('Jelentkezzen be az �j jelszav�val!');

                        },
                        error=> {
                        console.log(error);
                    })
    }
}
